<template>
    <div>
        <home v-if="active==0"></home>
        <rank v-if="active==1"></rank>
        <personfile v-if="active==2"></personfile>

        <van-tabbar v-model="active" >
            <van-tabbar-item icon="home-o">大厅</van-tabbar-item>
            <van-tabbar-item icon="search">排行榜</van-tabbar-item>
            <van-tabbar-item icon="friends-o">我的</van-tabbar-item>
        </van-tabbar>
    </div>

</template>

<script>
    import personfile from '../../../views/mine/Personfile'
    import home from '../../../views/home/Home'
    import rank from '../../../views/rank/Rank'
    export default {
        name: "Root",
        components:{
            personfile,
            home,
            rank
        },
        data(){
            return{
                active: 2,
            }
        },
        created(){
            let active=this.$route.query.active
            if(active!=undefined){
                this.active=this.$route.query.active
            }else {
                this.active=0
            }
        }
    }
</script>

