<template>
    <div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list>
        <van-cell v-for="(item,index) in content" :key="index">
            <router-link :to="{path:'/details_article',query:{id:item.news_id,name:item.news_user_name}}">
                <!--<van-cell :title="item.news_title" is-link class="cell"/>-->
                <van-cell :title="item.news_title" :label="'作者：'+item.news_user_name" />
            </router-link>
        </van-cell>
        </van-list>
            <van-cell></van-cell>
            <van-cell></van-cell>
            <van-cell></van-cell>
        </van-pull-refresh>
    </div>
</template>

<script>
    import { Toast } from 'vant'
    export default {
        name: "Community_blog",
        data(){
            return{
                content:[],
                isLoading:false
            }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/blog/findallblog',{
                params:{key:'vv9544'}
            }).then(res=>{
                if(res.status==200&&res.data.state){
                    this.content=res.data.moments
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        },
        methods:{
            onRefresh() {
                this.$axios.get('https://zhuzuoji.com/myapi/moments/findallmoments',{
                    params:{key:'vv9544'}
                }).then(res=>{

                    if(res.status==200&&res.data.state){
                        this.tx_img=res.data.moments
                    }
                })
                setTimeout(() => {
                    Toast('刷新成功');
                    this.isLoading = false;
                }, 1000);
            },
        }
    }
</script>

