<template>
    <div>
        <van-tabs v-model="active">
            <van-tab title="社区打卡">
                <community></community>
            </van-tab>
            <van-tab title="社区Blog">
                <community-bolg></community-bolg>
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
    import Community from '../../components/component/community_clock_in/Community_clock_in'
    import CommunityBolg from '../../components/component/community_clock_in/Community_blog'
    export default {
        name: "Home",
        components:{
            Community,
            CommunityBolg
        },
        data(){
            return{
                active:0,
            }
        },
        mounted() {
            let ac=Number(this.$route.query.active)
            if(ac===0){
                this.active=ac+1
            }

        }
    }
</script>

<style scoped>
    >>>.van-tabs__wrap{
        position: fixed;
        width: 100%;
        top: 0;
        height: 44px;
        z-index: 999;
    }
    >>>.van-tabs__content{
       margin-top: 44px;
    }
    >>>.van-tabs__nav--line{
        background-color: rgba(146,188,255,.3);
    }
</style>