<template>
    <div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list>
            <van-cell v-for="(item,index) in tx_img" :key="index">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #icon>
                    <van-image width="100" height="100" fit="cover" :src="item.mts_pic"/>
                </template>

                    <template #title>
                        <div style="text-align: left;font-size: 12px;
                    width: 150px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap">
                    <span class="custom-title" >标题：{{item.mts_content}}</span><br>
                        <span class="custom-title">作者：{{item.mts_user_name}}</span><br>
                        <span class="custom-title">时间：{{item.mts_time2.split(' ')[0]}}</span>
                        </div>
                    </template>
            </van-cell>

            </van-list>
            <van-cell></van-cell>
            <van-cell></van-cell>
            <van-cell></van-cell>
        </van-pull-refresh>
    </div>
</template>

<script>
    import { Toast } from 'vant'
    export default {
        name: "Community_clock_in",
        data(){
            return{
                tx_img:[],
                isLoading: false,
            }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/moments/findallmoments',{
                params:{key:'vv9544'}
            }).then(res=>{
                if(res.status==200&&res.data.state){
                    this.tx_img=res.data.moments
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        },
        methods:{
            onRefresh() {
                this.$axios.get('https://zhuzuoji.com/myapi/moments/findallmoments',{
                    params:{key:'vv9544'}
                }).then(res=>{
                    if(res.status==200&&res.data.state){
                        this.tx_img=res.data.moments
                    }
                })
                setTimeout(() => {
                    Toast('刷新成功');
                    this.isLoading = false;
                }, 1000);
            },
        }
    }
</script>

<style scoped>
    .custom-title{
        margin-left: 10px;
    }
</style>